import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { Text, View, TextInput, Button, StyleSheet } from "react-native";
import { addShoppingList } from "../reducers/shoppinglistsSlice";
const AddShoppingListForm = () => {
  const [text, setText] = useState();
  const dispatch = useDispatch();

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(addShoppingList({ title: text }));
    setText("");
  };

  return (
    <View>
      <TextInput placeholder="Add shopping list" onChangeText={setText} />
      <Button title="Add" onPress={submitHandler} />
    </View>
  );
};

export default AddShoppingListForm;
